										<CodeBlock code={`# Clone the repository
git clone https://github.com/byronwade/beam
cd beam

# Copy example configuration
cp .env.example .env

# Edit configuration
nano .env

# Required environment variables:
# BEAM_MODE=production
# BEAM_PEER_ID=my-peer-1
# BEAM_BOOTSTRAP_PEERS=bootstrap.beam.network:4000
# BEAM_TOR_ENABLED=true
# BEAM_DATA_DIR=/var/lib/beam

# Start Beam
docker compose -f docker-compose.prod.yml up -d

# Check status
docker compose ps
docker compose logs -f`} language="bash" />
